@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0px;
  padding: 0px;
}

.books {
  padding: 1rem;
  background: linear-gradient(to right, #ffff00, #ff7300);
  margin: 0px;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 12px;
}

.card {
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.books-item {
  padding: 2rem;
  background-color: #d00000;
  margin: 1rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 12px;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
}

.books-item__description {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 0px 0.5rem;
}

.books-item__title {
  font-size: 1.5rem;
  font-weight: 900;
}

.books-item__description h2,
h3 {
  margin: 10px;
  color: white;
}

.books-item__description h3 {
  font-style: italic;
}

.books-image {
  display: -webkit-flex;
  display: flex;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  margin-left: auto;
  margin-right: 0px;
}

.books-image img {
  height: 30vh;
  width: auto;
  border-radius: 10px;
}

@media (max-width: 1000px) {
  .books-item__title {
    font-size: 1.1rem;
  }
  .books-image img {
    height: 15vh;
  }
}

.genre {
  -webkit-align-items: center;
          align-items: center;
  margin: 20px 0px;
  padding: 0px 1.9rem;
}

.genre h2 {
  margin: 0.8rem 0px;
  text-align: center;
  color: black;
}

.genre select {
  width: 100%;
  height: 100%;
  font-size: 1.2rem;
  background-color: white;
  border: 3px solid transparent;
  border-color: #6a040f;
  color: #6a040f;
  font-weight: 600;
  border-radius: 4px;
  padding: 0.2rem;
}

.genre select:hover {
  background-color: #f8f8f8;
}

.genre option {
  font-size: 1.2rem;
  font-weight: 400;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 20px;
}

.header h2 {
  margin-left: 20px;
  display: inline-block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 2rem;
  text-align: center;
}

.header img {
  width: 60px;
  height: auto;
}

.App {
  margin: 15px;
}

